import React, { useState, useEffect } from 'react';
import ViewHeader from '../../shared/components/header/ViewHeader';
import ViewContent from '../../shared/components/content/ViewContent';
import { useHistory, useParams } from 'react-router-dom';
import {
    Card, Button, Form, FormGroup, Label, Input, Alert
} from 'reactstrap';
import axios from 'axios';

function AgregarUsuario(props) {
    const [role, setRole] = useState('periodista');
    const [nombre, setNombre] = useState('');
    const [apellido, setApellido] = useState('');
    const [usuarioId, setUsuarioId] = useState('');
    const [email, setEmail] = useState('');
    const [celular, setCelular] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState(false);
    const [errorMensaje, setErrorMensaje] = useState('');
    const history = useHistory();
    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    const roles = [{
        rolValor: 'periodista',
        rolNombre: 'Periodista'
    }, {
        rolValor: 'coordinador',
        rolNombre: 'Coordinador'
    }, {
        rolValor: 'jefe de prensa',
        rolNombre: 'Jefe de Prensa'
    }, {
        rolValor: 'admin',
        rolNombre: 'Administrador'
    }];

    const agregarUsuario = (e) => {
        e.preventDefault();
        const usuario = {
            role,
            nombre,
            apellido,
            email,
            celular,
            password,
            usuario: usuarioId
        }
        axios.post(`${process.env.REACT_APP_API_URL}/api/usuario/crear`, usuario, axiosConfig)
            .then((response) => {
                history.push('/usuarios')
            })
            .catch((error) => {
                setError(true)
                setErrorMensaje(error.response.data.message ? error.response.data.message : 'Hubo un error al agregar el usuario');
            });
    }
    

    useEffect(() => {
        
    }, []);

    return (
        <div className="view">
            <ViewHeader/>
            <ViewContent>
                <Card className="mb-4">
                    <h6 className="mb-4 text-uppercase">Agregar Usuario</h6>
                    { error &&  
                        <Alert color="danger">
                            <strong>Error!</strong> { errorMensaje }
                        </Alert>}
                    <Form onSubmit={ agregarUsuario } className="insertar-nota-form">
                        <FormGroup>
                            <Label for="role"></Label>
                            <Input type="select" name="role" id="role" placeholder="Rol" onChange={ ($event) => {setError(false);setRole($event.target.value)}}>
                                { roles.map((rol, index) => <option key={index} value={rol.rolValor}>{ rol.rolNombre }</option>)}
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="nombre">Nombre</Label>
                            <Input type="text" name="nombre" id="nombre" placeholder="Nombre" onChange={($event) => {setError(false);setNombre($event.target.value)}}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="apellido">Apellido</Label>
                            <Input type="text" name="apellido" id="apellido" placeholder="Apellido" onChange={($event) => {setError(false);setApellido($event.target.value)}}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="usuario">Usuario</Label>
                            <Input type="text" name="usuario" id="usuario" placeholder="Usuario" onChange={($event) => {setError(false);setUsuarioId($event.target.value)}}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="password">Password</Label>
                            <Input type="password" name="password" id="password" placeholder="Password" onChange={($event) => {setError(false);setPassword($event.target.value)}}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="email">Email</Label>
                            <Input type="email" name="email" id="email" placeholder="E-mail" onChange={($event) => {setError(false);setEmail($event.target.value)}}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="celular">Celular</Label>
                            <Input type="number" name="celular" id="celular" placeholder="Celular" onChange={($event) => {setError(false);setCelular($event.target.value)}}/>
                        </FormGroup>
                        <div className="mb-5 botones-agregar-usuario">
                            <Button color="primary" type="submit" style={{marginRight: '10px'}}>Agregar usuario</Button>
                            <Button color="warning" className="boton-cancelar" onClick={() => history.push('/usuarios')}>Cancelar</Button>
                        </div>
                    </Form>
                </Card>
            </ViewContent>
        </div>
    );
}

export default AgregarUsuario;