import React, { useState, useEffect } from 'react';
import ViewHeader from '../../shared/components/header/ViewHeader';
import ViewContent from '../../shared/components/content/ViewContent';
import { useHistory, useParams } from 'react-router-dom';
import {
    Card, Button, Form, FormGroup, Label, Input, Alert
} from 'reactstrap';
import axios from 'axios';

function ModificarUsuario(props) {
    const [role, setRole] = useState('periodista');
    const [nombre, setNombre] = useState('');
    const [apellido, setApellido] = useState('');
    const [usuarioId, setUsuarioId] = useState('');
    const [email, setEmail] = useState('');
    const [celular, setCelular] = useState('');
    const [error, setError] = useState(false);
    const [errorMensaje, setErrorMensaje] = useState('');
    const currentUserId = useParams().id;
    const history = useHistory();
    const axiosConfig = {
        headers: {
            'Authorization': localStorage.getItem('token')
        }
    };

    const roles = [{
        rolValor: 'periodista',
        rolNombre: 'Periodista'
    }, {
        rolValor: 'coordinador',
        rolNombre: 'Coordinador'
    }, {
        rolValor: 'jefe de prensa',
        rolNombre: 'Jefe de Prensa'
    }, {
        rolValor: 'admin',
        rolNombre: 'Administrador'
    }];

    const modificarUsuario = (e) => {
        e.preventDefault();
        const usuario = {
            role,
            nombre,
            apellido,
            email,
            celular,
            usuario: usuarioId,
            id: currentUserId
        }

        axios.put(`${process.env.REACT_APP_API_URL}/api/usuario/editar`, usuario, axiosConfig)
            .then((response) => {
                history.push('/usuarios')
            })
            .catch((error) => {
                setError(true)
                setErrorMensaje(error.response.data.message ? error.response.data.message : 'Hubo un error al modificar el usuario');
            });
    }
    

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_API_URL}/api/usuario/id/${currentUserId}`, axiosConfig)
            .then((response) => {
                const { role, nombre, apellido, email, celular } = response.data.usuario;
                const usuarioId = response.data.usuario.usuario;
                setRole(role);
                setNombre(nombre);
                setApellido(apellido);
                setEmail(email);
                setCelular(celular);
                setUsuarioId(usuarioId);
            });
    }, []);

    return (
        <div className="view">
            <ViewHeader/>
            <ViewContent>
                <Card className="mb-4">
                    <h6 className="mb-4 text-uppercase">Modificar Usuario</h6>
                    { error &&  
                        <Alert color="danger">
                            <strong>Error!</strong> { errorMensaje }
                        </Alert>}
                    <Form onSubmit={ modificarUsuario } className="insertar-nota-form">
                        <FormGroup>
                            <Label for="role"></Label>
                            <Input type="select" name="role" id="role" placeholder="Rol" onChange={ ($event) => {setError(false);setRole($event.target.value)}} value={role}>
                                { roles.map((rol, index) => <option key={index} value={rol.rolValor}>{ rol.rolNombre }</option>)}
                            </Input>
                        </FormGroup>
                        <FormGroup>
                            <Label for="nombre">Nombre</Label>
                            <Input type="text" name="nombre" id="nombre" placeholder="Nombre" onChange={($event) => {setError(false);setNombre($event.target.value)}} value={nombre}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="apellido">Apellido</Label>
                            <Input type="text" name="apellido" id="apellido" placeholder="Apellido" onChange={($event) => {setError(false);setApellido($event.target.value)}} value={apellido}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="usuario">Usuario</Label>
                            <Input type="text" name="usuario" id="usuario" placeholder="Usuario" onChange={($event) => {setError(false);setUsuarioId($event.target.value)}} value={usuarioId}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="email">Email</Label>
                            <Input type="email" name="email" id="email" placeholder="E-mail" onChange={($event) => {setError(false);setEmail($event.target.value)}} value={email}/>
                        </FormGroup>
                        <FormGroup>
                            <Label for="celular">Celular</Label>
                            <Input type="number" name="celular" id="celular" placeholder="Celular" onChange={($event) => {setError(false);setCelular($event.target.value)}} value={celular}/>
                        </FormGroup>
                        <div className="mb-5 botones-agregar-usuario">
                            <Button color="primary" type="submit" style={{marginRight: '10px'}}>Modificar usuario</Button>
                            <Button color="warning" className="boton-cancelar" onClick={() => history.push('/usuarios')}>Cancelar</Button>
                        </div>
                    </Form>
                </Card>
            </ViewContent>
        </div>
    );
}

export default ModificarUsuario;